.header-logo {
  display: inline-block;

  margin-right: 160px;
  cursor: pointer;

  @include vp-374 {
    margin-right: 124px;
  }
}

.header-logo__mobile {
  display: none;

  @include vp-767 {
    display: block;
  }
}

.header-logo__desktop {
  display: block;

  @include vp-767 {
    display: none;
  }
}
