@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "PTMono";
  font-display: swap;
  src: url("../../fonts/PTMono-Regular.woff2") format("woff2"), url("../../fonts/PTMono-Regular.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Roboto Condensed";
  font-display: swap;
  src: url("../../fonts/RobotoCondensed-Regular.woff2") format("woff2"), url("../../fonts/RobotoCondensed-Regular.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "Roboto Condensed";
  font-display: swap;
  src: url("../../fonts/RobotoCondensed-Bold.woff2") format("woff2"), url("../../fonts/RobotoCondensed-Bold.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 500;
  font-family: "Roboto";
  font-display: swap;
  src: url("../../fonts/Roboto-Medium.woff2") format("woff2"), url("../../fonts/Roboto-Medium.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "PTMono", "Arial", sans-serif;
  color: #000000;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #ffffff;
}

a {
  color: #000000;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.container {
  width: 100%;
  max-width: 1300px;
  margin: 0 auto;
  padding: 0 60px;
}
@media (max-width: 1439px) {
  .container {
    padding: 0 50px;
  }
}
@media (max-width: 767px) {
  .container {
    padding: 0 25px;
  }
}

.btn {
  display: inline-flex;
  padding: 10px 20px;
  color: #ffffff;
  background-color: #2c39f2;
  border: none;
  cursor: pointer;
  transition: opacity 0.3s ease;
}
.btn--red {
  background-color: #ff1553;
}
.btn:disabled {
  opacity: 0.5;
  pointer-events: none;
}
@media (hover: hover) {
  .btn:hover:not(.focus-visible) {
    opacity: 0.8;
  }
}
.btn.focus-visible:focus {
  opacity: 0.8;
}

.modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1000;
  display: flex;
  flex-direction: column;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
  background-color: rgba(0, 0, 0, 0.8);
  transform: scale(1.2);
  visibility: hidden;
  opacity: 0;
  transition: opacity 0.6s cubic-bezier(0.55, 0, 0.1, 1), transform 0.6s cubic-bezier(0.55, 0, 0.1, 1), visibility 0.6s cubic-bezier(0.55, 0, 0.1, 1);
  pointer-events: none;
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.modal::-webkit-scrollbar {
  display: none;
}
.modal__wrapper {
  position: relative;
  display: flex;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  width: 100%;
  min-height: 100%;
  padding: 60px 40px;
}
@media (max-width: 767px) {
  .modal__wrapper {
    padding: 60px 16px;
  }
}
.modal__overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  cursor: pointer;
}
.modal__content {
  position: relative;
  width: 600px;
  padding: 40px;
  background-color: #ffffff;
}
@media (max-width: 767px) {
  .modal__content {
    width: 100%;
    padding: 20px;
  }
}
.modal__close-btn {
  position: absolute;
  top: 22px;
  right: 22px;
  z-index: 1;
  width: 40px;
  height: 40px;
  padding: 0;
  background: rgba(255, 255, 255, 0);
  border: none;
  cursor: pointer;
}
.modal--responsive .modal__content {
  width: 100%;
}
.modal--fit-content .modal__content {
  width: auto;
}
@media (max-width: 767px) {
  .modal--fit-content .modal__content {
    width: 100%;
  }
}
.modal--no-scale {
  transform: none;
}
.modal--preload {
  transition: none;
}
.modal.is-active {
  transform: scale(1);
  visibility: visible;
  opacity: 1;
  pointer-events: auto;
}

.header {
  position: fixed;
  z-index: 1;
  width: 100%;
  background: linear-gradient(180deg, #041827 84.9%, rgba(4, 24, 39, 0) 100%);
}

.header__container {
  display: flex;
  align-items: center;
  padding-top: 30px;
  padding-bottom: 35px;
}
@media (max-width: 1439px) {
  .header__container {
    justify-content: space-between;
    padding-top: 20px;
    padding-bottom: 25px;
  }
}
@media (max-width: 767px) {
  .header__container {
    padding-top: 15px;
    padding-bottom: 20px;
  }
}

.header-logo {
  display: inline-block;
  margin-right: 160px;
  cursor: pointer;
}
@media (max-width: 374px) {
  .header-logo {
    margin-right: 124px;
  }
}

.header-logo__mobile {
  display: none;
}
@media (max-width: 767px) {
  .header-logo__mobile {
    display: block;
  }
}

.header-logo__desktop {
  display: block;
}
@media (max-width: 767px) {
  .header-logo__desktop {
    display: none;
  }
}

.header-contacts {
  display: flex;
}

.header-contacts__email {
  position: relative;
  display: flex;
  align-items: center;
  margin-right: 30px;
  padding-right: 60px;
  padding-left: 35px;
}
@media (max-width: 1439px) {
  .header-contacts__email {
    justify-content: center;
    width: 40px;
    height: 40px;
    margin-right: 20px;
    padding: 0;
  }
}

.header-contacts__email span {
  line-height: 18px;
  color: #f2f5f6;
}
@media (max-width: 1439px) {
  .header-contacts__email span {
    display: none;
  }
}

.header-contacts__email svg {
  position: absolute;
  left: 0;
}
@media (max-width: 1439px) {
  .header-contacts__email svg {
    position: relative;
  }
}

.header-contacts__phone {
  position: relative;
  display: flex;
  align-items: center;
  margin-right: 145px;
  padding-right: 40px;
  padding-left: 35px;
}
@media (max-width: 1439px) {
  .header-contacts__phone {
    justify-content: center;
    width: 40px;
    height: 40px;
    margin-right: 40px;
    padding-right: 0;
    padding-left: 0;
  }
}
@media (max-width: 1279px) {
  .header-contacts__phone {
    margin-right: 20px;
  }
}
@media (max-width: 767px) {
  .header-contacts__phone {
    margin-right: 0;
  }
}

.header-contacts__phone span {
  line-height: 18px;
  color: #f2f5f6;
}
@media (max-width: 1439px) {
  .header-contacts__phone span {
    display: none;
  }
}

.header-contacts__phone svg {
  position: absolute;
  left: 0;
}
@media (max-width: 1439px) {
  .header-contacts__phone svg {
    position: relative;
  }
}

.header-contacts__button {
  padding: 10px 15px;
  border: 1px solid #f2f5f6;
  border-radius: 3px;
  background-color: transparent;
}
@media (max-width: 1279px) {
  .header-contacts__button {
    display: none;
  }
}

.header-contacts__button span {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 14px;
  line-height: 16px;
  font-weight: 700;
  text-transform: uppercase;
  color: #f2f5f6;
}

.intro {
  position: relative;
  width: 100%;
  height: 100%;
}

.intro__background {
  position: absolute;
  top: 0;
  z-index: -1;
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
}
@media (max-width: 1023px) {
  .intro__background {
    object-position: -130px;
  }
}
@media (max-width: 374px) {
  .intro__background {
    object-position: -290px;
  }
}

.intro__container {
  padding-left: 185px;
  padding-bottom: 280px;
}
@media (max-width: 1439px) {
  .intro__container {
    bottom: 100px;
    padding-left: 185px;
    padding-bottom: 280px;
  }
}
@media (max-width: 1023px) {
  .intro__container {
    padding: 0 50px;
    padding-bottom: 205px;
  }
}
@media (max-width: 767px) {
  .intro__container {
    padding-bottom: 190px;
  }
}

.intro__title {
  margin: 0;
  padding-top: 305px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-weight: 700;
  font-size: 80px;
  line-height: 94px;
  letter-spacing: 0.07em;
  color: #f2f5f7;
}
@media (max-width: 1439px) {
  .intro__title {
    padding-top: 305px;
  }
}
@media (max-width: 1023px) {
  .intro__title {
    padding-top: 180px;
    font-size: 64px;
    line-height: 75px;
  }
}
@media (max-width: 767px) {
  .intro__title {
    font-size: 36px;
    line-height: 42px;
  }
}

.intro__description {
  max-width: 685px;
  margin: 0;
  padding-top: 40px;
  font-size: 22px;
  line-height: 34px;
  color: #f2f5f7;
}
@media (max-width: 1023px) {
  .intro__description {
    padding-top: 20px;
  }
}
@media (max-width: 767px) {
  .intro__description {
    font-size: 14px;
    line-height: 22px;
  }
}

.intro__button {
  padding: 0;
  margin-top: 40px;
  border: 0;
  border-radius: 3px;
  background-color: #00b2ff;
}

.intro__button p {
  margin: 0;
  padding: 17px 27px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-weight: 700;
  font-size: 18px;
  line-height: 21px;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: #f2f5f7;
}
@media (max-width: 767px) {
  .intro__button p {
    font-size: 14px;
    line-height: 16px;
  }
}

.footer {
  padding-top: 50px;
  padding-bottom: 55px;
  background-color: #041827;
  color: #b4babe;
}
@media (max-width: 1279px) {
  .footer {
    padding-bottom: 65px;
  }
}

.footer a {
  color: #b4babe;
}

.footer__container {
  display: flex;
  max-width: 1300px;
  margin: 0 auto;
  padding: 0 60px;
}
@media (max-width: 1279px) {
  .footer__container {
    display: grid;
    grid-template-areas: "social social" "sections contacts";
    justify-content: space-between;
    margin: 0 auto;
    padding: 0 50px;
  }
}
@media (max-width: 767px) {
  .footer__container {
    grid-template-areas: "social social" "sections sections" "contacts contacts";
    padding: 0;
  }
}

@media (max-width: 767px) {
  .footer__accordion--hide {
    display: none;
  }
}

@media (max-width: 767px) {
  .footer__accorion--close::before {
    content: "";
    position: absolute;
    right: 25px;
    top: 27px;
    width: 15px;
    height: 1px;
    transform: rotate(90deg);
    background-color: #afb8be;
  }
}

.footer-social {
  grid-area: social;
  padding-right: 160px;
}
@media (max-width: 1279px) {
  .footer-social {
    display: flex;
    justify-content: space-between;
    padding-right: 0;
  }
}
@media (max-width: 767px) {
  .footer-social {
    flex-direction: column;
    padding-bottom: 25px;
  }
}

@media (max-width: 767px) {
  .footer-social__logo img {
    margin: 0 auto;
  }
}

.footer-social__list {
  display: flex;
  margin: 0;
  padding: 0;
  padding-top: 30px;
  list-style-type: none;
}
@media (max-width: 1279px) {
  .footer-social__list {
    padding-top: 0;
  }
}
@media (max-width: 767px) {
  .footer-social__list {
    justify-content: center;
    padding-top: 15px;
  }
}

.footer-social__item {
  padding-left: 15px;
}
@media (max-width: 1279px) {
  .footer-social__item {
    display: flex;
    align-items: center;
  }
}

.footer-social__icon {
  display: flex;
}

.footer-sections {
  grid-area: sections;
  padding-top: 5px;
}
@media (max-width: 1279px) {
  .footer-sections {
    padding-top: 35px;
  }
}
@media (max-width: 767px) {
  .footer-sections {
    padding: 0;
    border-top: 1px solid #afb8be;
  }
}

.footer-sections__title {
  position: relative;
  margin: 0;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 15px;
  line-height: 16px;
  font-weight: 400;
  letter-spacing: 0.05em;
  text-transform: uppercase;
}
@media (max-width: 767px) {
  .footer-sections__title {
    padding: 20px 25px;
    cursor: pointer;
  }
  .footer-sections__title::after {
    content: "";
    position: absolute;
    right: 25px;
    top: 27px;
    width: 15px;
    height: 1px;
    background-color: #afb8be;
  }
}

.footer-sections__container {
  display: flex;
  padding-top: 20px;
}
@media (max-width: 1279px) {
  .footer-sections__container {
    flex-direction: column;
  }
}
@media (max-width: 767px) {
  .footer-sections__container {
    padding: 0 25px 20px;
  }
}

.footer-sections__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
}
.footer-sections__list--first {
  min-width: 220px;
  margin-right: 30px;
}
.footer-sections__list--last {
  margin-right: 125px;
}
@media (max-width: 1279px) {
  .footer-sections__list {
    margin-right: 0;
  }
  .footer-sections__list--last {
    padding-top: 10px;
  }
}

.footer-sections__item {
  padding-top: 10px;
  line-height: 22px;
}
.footer-sections__item:nth-child(1) {
  padding-top: 0;
}
@media (max-width: 767px) {
  .footer-sections__item {
    font-size: 14px;
    line-height: 19px;
  }
}

.footer-contacts {
  grid-area: contacts;
  padding-top: 5px;
}
@media (max-width: 1279px) {
  .footer-contacts {
    padding-top: 35px;
  }
}
@media (max-width: 767px) {
  .footer-contacts {
    padding: 0;
    border-top: 1px solid #afb8be;
    border-bottom: 1px solid #afb8be;
  }
}

.footer-contacts__container {
  display: flex;
  flex-direction: column;
}
@media (max-width: 767px) {
  .footer-contacts__container {
    padding: 0 25px 20px;
  }
}

.footer-contacts__title {
  position: relative;
  margin: 0;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 15px;
  line-height: 16px;
  font-weight: 400;
  letter-spacing: 0.05em;
  text-transform: uppercase;
}
@media (max-width: 767px) {
  .footer-contacts__title {
    padding: 20px 25px;
    cursor: pointer;
  }
  .footer-contacts__title::after {
    content: "";
    position: absolute;
    right: 25px;
    top: 27px;
    width: 15px;
    height: 1px;
    background-color: #afb8be;
  }
}

.footer-contacts__address {
  padding-top: 20px;
  font-style: normal;
  line-height: 22px;
}
@media (max-width: 1279px) {
  .footer-contacts__address {
    max-width: 310px;
  }
}
@media (max-width: 767px) {
  .footer-contacts__address {
    padding-top: 0;
    font-size: 14px;
    line-height: 19px;
  }
}

.footer-contacts__phone {
  padding-top: 10px;
  line-height: 25px;
}
@media (max-width: 767px) {
  .footer-contacts__phone {
    font-size: 14px;
  }
}

